import React, { useState, useEffect, useRef } from "react";
import axios from "axios";
import * as THREE from "three";
import NET from "vanta/dist/vanta.net.min";

export const SzallasList = () => {
  const [data, setData] = useState([]);
  const [error, setError] = useState("");
  const vantaRef = useRef(null);

  useEffect(() => {
    const vantaEffect = NET({
      el: vantaRef.current,
      THREE,
      color: 0xff4500, // Karácsonyi piros
      backgroundColor: 0x001f3f, // Mélykék háttér
      points: 12.0,
      maxDistance: 20.0,
      spacing: 18.0,
    });

    return () => {
      if (vantaEffect) vantaEffect.destroy();
    };
  }, []);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const token = localStorage.getItem("jwt");
        if (!token) {
          throw new Error("Nem található JWT token!");
        }
        const valasz = await axios.get("https://szallasjwt.sulla.hu/data", {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        });
        setData(valasz.data);
      } catch (error) {
        setError(
          "Az adatok lekérése sikertelen. Lehet, hogy nem vagy bejelentkezve?"
        );
        console.error("Hiba az adatok lekérése során: ", error);
      }
    };
    fetchData();
  }, []);

  return (
    <div
      ref={vantaRef}
      style={{ height: "100vh", width: "100%", overflow: "auto", position: "relative" }}
    >
      <div style={styles.container}>
        <h1 style={styles.title}>Szállások listája</h1>
        {error && <p style={styles.error}>{error}</p>}
        {data.length > 0 ? (
          <ul style={styles.list}>
            {data.map((item) => (
              <li key={item.id} style={styles.listItem}>
                <strong>{item.name}</strong> - {item.hostname} - {item.location} - {" "}
                <span style={styles.price}>{item.price} Ft</span> - {item.minimum_nights} éjszaka minimum
              </li>
            ))}
          </ul>
        ) : (
          <p style={styles.noData}>Nem találhatók az adatok!</p>
        )}
      </div>
    </div>
  );
};

const styles = {
  container: {
    position: "absolute",
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
    textAlign: "center",
    backgroundColor: "rgba(255, 255, 255, 0.9)",
    borderRadius: "15px",
    padding: "20px 30px",
    boxShadow: "0 4px 8px rgba(0, 0, 0, 0.2)",
    maxWidth: "800px",
    width: "90%",
    maxHeight: "80vh",
    overflow: "auto",
  },
  title: {
    color: "#ff4500", // Karácsonyi piros
    marginBottom: "20px",
  },
  error: {
    color: "red",
    marginBottom: "20px",
  },
  list: {
    listStyleType: "none",
    padding: 0,
    textAlign: "left",
  },
  listItem: {
    padding: "10px",
    borderBottom: "1px solid #ccc",
  },
  price: {
    color: "#28a745", // Zöld ár kiemelés
    fontWeight: "bold",
  },
  noData: {
    color: "#333",
    fontSize: "16px",
  },
};
